.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
.underline{
  background-color: #61dafb;
  height: 4px;
  width: 120px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  max-width: 600px;
  margin: 0 auto;
}

.job-info{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
}
.company{
  color: #555;
  font-weight: bold;
  background-color:#76b1c2;
  width:min-content;
  padding: 5px;
}
p{color: #777;}
.duty-container{
  display: flex;
  align-items: center;
}
.icon{
  color: #61dafb;
  font-size: 30px;
  margin-right: 20px;
}
.duty{
  color: #444;
}
.more-info{
  width:fit-content;
  padding: 10px 50px;
  text-transform: uppercase;
  margin: 25px auto;
  color: #fff;
  background-color: #224f5c;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  font-size: 18px;
}
.btn-container{
  margin: 40px auto 0;
}
.btn{
  text-transform: uppercase;
  margin-left: 20px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  background: none;
  font-size: 20px;
}
.active-btn{
  color: #36a9c9;
  border-bottom: 2px solid #51c5e6;
}